.Popup {
  position: fixed;
  top: 40%;
  left: 50%;
  transform: translate(-50%, -50%);
  max-width: 98%;
  min-width: 360px;
  min-height: 200px;
  padding: 24px;
  border-radius: 16px;
  background: #fff;
  transition: 0.15s;
  z-index: 100000;
}
.Popup-body {
  position: relative;
}
.Popup .Popup-cross {
  position: absolute;
  right: 8px;
  top: 10px;
  padding: 0 4px;
  font-size: 20px;
  cursor: pointer;
}
.Popup--Confirm-description {
  display: block;
}
.Popup--Confirm-actions {
  display: block;
}
.BackDrop--Popup .BackDrop-content > div:not(:last-of-type) {
  pointer-events: none;
  opacity: 0.02;
}
