.Spinner {
  height: 20px;
  width: 20px;
  border-radius: 50%;
  animation: spin 0.8s infinite linear;
  display: none;
  opacity: 0;
  border: 2px solid #fff;
  border-top-color: transparent;
  vertical-align: middle;
  transition: 0.3s 0.05s;
  transform: none;
}
.Spinner--primary {
  border-color: #ff3f62;
  border-top-color: transparent;
}
.Spinner--show {
  display: inline-block;
  opacity: 1;
}
@-moz-keyframes spin {
  100% {
    transform: rotate(360deg);
  }
}
@-webkit-keyframes spin {
  100% {
    transform: rotate(360deg);
  }
}
@-o-keyframes spin {
  100% {
    transform: rotate(360deg);
  }
}
@keyframes spin {
  100% {
    transform: rotate(360deg);
  }
}
