.RipplePointer {
  position: relative;
  width: 56px;
  height: 56px;
}
.RipplePointer::before,
.RipplePointer::after {
  content: '';
  display: block;
  position: absolute;
  width: 100%;
  height: 100%;
  left: -28px;
  top: -28px;
  border-radius: 50%;
  background: #fff;
  opacity: 0;
  animation: 1.6s cubic-bezier(0, 0, 0.46, 1) 0s infinite;
  transform: scale(0);
  pointer-events: none;
}
.RipplePointer::before {
  z-index: 1;
  animation-name: ripple-before;
}
.RipplePointer::after {
  animation-name: ripple-after;
  animation-delay: 0.6s;
  animation-duration: 1.6s;
  z-index: 2;
}
.RipplePointer .RipplePointer-epicenter {
  display: block;
  height: 24px;
  width: 24px;
  position: absolute;
  pointer-events: none;
  left: -12px;
  top: -12px;
  z-index: 3;
}
.RipplePointer .RipplePointer-epicenter::after {
  content: '';
  display: block;
  height: 16px;
  position: absolute;
  width: 16px;
  left: 4px;
  top: 4px;
  border-radius: 50%;
  opacity: 0.9;
  background-color: #fff;
}
.RipplePointer.RipplePointer--clickable .RipplePointer-epicenter {
  cursor: pointer;
  pointer-events: all;
}
.RipplePointer--primary::after,
.RipplePointer--primary::before {
  background: #ff3f62;
}
.RipplePointer-epicenter .RipplePointer--primary::after {
  background-color: #ff3f62;
}
@-moz-keyframes ripple-before {
  0% {
    transform: scale(0);
    opacity: 0.4;
  }
  100% {
    transform: scale(2);
    opacity: 0;
  }
}
@-webkit-keyframes ripple-before {
  0% {
    transform: scale(0);
    opacity: 0.4;
  }
  100% {
    transform: scale(2);
    opacity: 0;
  }
}
@-o-keyframes ripple-before {
  0% {
    transform: scale(0);
    opacity: 0.4;
  }
  100% {
    transform: scale(2);
    opacity: 0;
  }
}
@keyframes ripple-before {
  0% {
    transform: scale(0);
    opacity: 0.4;
  }
  100% {
    transform: scale(2);
    opacity: 0;
  }
}
@-moz-keyframes ripple-after {
  0% {
    transform: scale(0);
    opacity: 0.4;
  }
  80%, 100% {
    transform: scale(2);
    opacity: 0;
  }
}
@-webkit-keyframes ripple-after {
  0% {
    transform: scale(0);
    opacity: 0.4;
  }
  80%, 100% {
    transform: scale(2);
    opacity: 0;
  }
}
@-o-keyframes ripple-after {
  0% {
    transform: scale(0);
    opacity: 0.4;
  }
  80%, 100% {
    transform: scale(2);
    opacity: 0;
  }
}
@keyframes ripple-after {
  0% {
    transform: scale(0);
    opacity: 0.4;
  }
  80%, 100% {
    transform: scale(2);
    opacity: 0;
  }
}
