.Clipboard--withTooltip .Field--Textarea {
  width: 0;
  height: 0;
  position: absolute;
  left: -9999px;
}
.Clipboard--withTooltip.Clipboard--copied .Tooltip-text {
  background: #55d4a1;
}
.Clipboard--withButton .Field--Textarea,
.Clipboard--withButton .Btn {
  display: inline-block;
  vertical-align: top;
}
.Clipboard--withButton.Clipboard--copied .Btn--Clipboard-copyButton {
  background: #55d4a1;
}
