.Banner {
  width: 100%;
  padding: 8px 12px;
  position: relative;
  border-radius: 3px;
  border: 1px solid #000;
  background-color: #fff;
  color: #000;
}
.Banner--green {
  border: 1px solid #439c53;
  background-color: #d4ffdc;
  color: #008000;
}
.Banner--red {
  border: 1px solid #9e3342;
  background-color: #ffd4da;
  color: #f00;
}
.Banner--yellow {
  border: 1px solid #ceb65b;
  background-color: #fff6d4;
}
