.ToastsContainer {
  position: fixed;
  top: 20px;
  left: 50%;
  transform: translateX(-50%);
  z-index: 100000;
}
.ToastsContainer > div:not(:first-of-type) {
  margin-top: 16px;
}
.ToastsContainer .isHidden {
  display: none;
}
.Toast {
  position: relative;
  width: 480px;
  padding: 12px 32px 12px 12px;
  font-size: 16px;
  text-align: center;
  border-radius: 3px;
  transform: translateY(-50%);
  animation: fadeInDownAppear 180ms ease-out 0s 1 normal forwards;
  transition: 0.15s;
  background: #fff;
  color: #000;
  box-shadow: 1px 1px 8px rgba(0,0,0,0.08);
}
.Toast--success {
  background: #90ee90;
  color: #008000;
}
.Toast--error {
  background: #8b0000;
  color: #fff;
}
.Toast .Toast-content {
  line-height: 20px;
}
.Toast .Toast-cross {
  position: absolute;
  right: 8px;
  top: 10px;
  padding: 0 4px;
  font-size: 20px;
  cursor: pointer;
}
@-moz-keyframes fadeInDownAppear {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
    transform: translateY(0);
  }
}
@-webkit-keyframes fadeInDownAppear {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
    transform: translateY(0);
  }
}
@-o-keyframes fadeInDownAppear {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
    transform: translateY(0);
  }
}
@keyframes fadeInDownAppear {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
    transform: translateY(0);
  }
}
