.Dropdown {
  position: relative;
  font-size: 14px;
  height: 32px;
  line-height: 30px;
  border: 1px solid #000;
}
.Dropdown .Dropdown-trigger {
  position: relative;
  cursor: pointer;
  z-index: 1;
  padding: 0 12px;
}
.Dropdown .Dropdown-trigger::after {
  content: '';
  position: absolute;
  left: 0;
  height: 12px;
  width: 100%;
  z-index: -1;
  display: none;
}
.Dropdown--show .Dropdown-trigger::after {
  display: block;
}
.DropdownBody {
  position: fixed;
  transform-origin: top center;
  top: 100%;
  left: 0;
  margin-top: 4px;
  min-width: 180px;
  max-width: 360px;
  border: 1px solid #ccc;
  will-change: transform;
  opacity: 0;
  z-index: 2;
}
.DropdownBody::before,
.DropdownBody::after {
  content: '';
  position: absolute;
  width: 0;
  height: 0;
}
.DropdownBody::before {
  top: -9px;
  left: 20px;
  border-left: 10px solid transparent;
  border-right: 10px solid transparent;
  border-bottom: 12px solid #ccc;
  z-index: -1;
}
.DropdownBody::after {
  top: -8px;
  left: 24px;
  border-left: 6px solid transparent;
  border-right: 6px solid transparent;
  border-bottom: 8px solid #fff;
  z-index: 1;
}
.DropdownOptions {
  max-height: 320px;
  overflow-y: auto;
}
.DropdownOptions > div {
  display: block;
  width: 100%;
  line-height: 24px;
  background: #fff;
  border: none;
}
.DropdownOptions > div:not(:last-child) {
  border-bottom: 1px solid #ddd;
}
.DropdownOptions .DropdownItem {
  padding: 4px 12px;
  text-align: left;
  outline: none;
}
