.FieldWrapper {
  position: relative;
}
.FieldWrapper .Field-label {
  font-size: 14px;
  font-weight: bold;
  display: block;
}
.FieldWrapper .Field-label .Field-label-star {
  color: #e34c2a;
}
.FieldWrapper .Field-content {
  width: 200px;
  display: block;
  font-size: 14px;
}
.FieldWrapper .Field-description {
  display: block;
}
.FieldWrapper .Field-error {
  display: none;
}
.FieldWrapper.FieldWrapper--disabled {
  display: block;
}
.FieldWrapper.FieldWrapper--disabled .Field-label,
.FieldWrapper.FieldWrapper--disabled .Field-el {
  cursor: not-allowed;
  opacity: 0.4;
}
.FieldWrapper.FieldWrapper--focused .Field-el {
  border-color: #528ff0;
}
.FieldWrapper.FieldWrapper--invalid {
  display: block;
}
.FieldWrapper.FieldWrapper--invalid .Field-el {
  border-color: #e34c2a;
}
.FieldWrapper.FieldWrapper--invalid .Field-error {
  display: block;
}
.Field {
  position: relative;
  display: block;
}
.Field .Field-el {
  width: 100%;
  height: 34px;
  border: 1px solid #000;
  padding: 0 8px;
  line-height: 34px;
  border-radius: 2px;
  outline: none;
}
