.BackDrop {
  position: relative;
}
.BackDrop::before {
  content: '';
  background: rgba(0,0,0,0.65);
  position: fixed;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  pointer-events: none;
  opacity: 0;
  transition: 0.15s all;
}
.BackDrop .BackDrop-content {
  position: relative;
}
.BackDrop--show {
  z-index: 99999;
}
.BackDrop--show::before {
  pointer-events: all;
  opacity: 1;
}
.freeze {
  overflow: hidden;
}
