.Btn {
  background: #fbfbfb;
  color: #414141;
  padding: 8px 20px;
  line-height: 16px;
  border-radius: 2px;
  border: none;
  outline: none;
  transition: 0.15s;
  transform-origin: center;
  cursor: pointer;
  font-size: 16px;
  box-shadow: inset 0 -2px 0 rgba(34,45,57,0.15);
}
.Btn:not(:disabled):hover {
  background-color: #ececec;
}
.Btn:not(:disabled):active {
  background-color: #dcdcdc;
}
.Btn:disabled {
  cursor: not-allowed !important;
  opacity: 0.5;
}
.Btn--link {
  padding: 8px 0;
  background-color: transparent;
  box-shadow: none;
  color: #000;
}
.Btn--link:not(:disabled):hover {
  background-color: inherit;
  text-decoration: underline;
}
.Btn--link:not(:disabled):active {
  background-color: inherit;
  text-decoration: underline;
}
.Btn--primary {
  background-color: #ff3f62;
  color: #fff;
}
.Btn--primary:not(:disabled):hover {
  background-color: #ff5675;
}
.Btn--primary:not(:disabled):active {
  background-color: #f5395b;
}
.Btn--round {
  border-radius: 24px;
}
.Btn--AsyncBtn .Btn-pendingText {
  transition: 0.3s 0.05s;
  display: none;
  opacity: 0;
}
.Btn--AsyncBtn .Btn-pendingText + .Spinner {
  margin-left: 8px;
}
.Btn--AsyncBtn .Btn-mainText {
  transition: 0.3s 0.05s;
}
.Btn--AsyncBtn--isPending {
  height: 34px;
}
.Btn--AsyncBtn--isPending .Btn-pendingText {
  display: unset;
  opacity: 1;
}
.Btn--AsyncBtn--isPending .Btn-mainText {
  display: none;
  opacity: 0;
}
