.GhostLoader {
  width: 100%;
  height: 100%;
  min-height: 12px;
  position: relative;
  background: #ebecf2;
}
.GhostLoader--animate {
  background: linear-gradient(to right, #ebecf2 0%, #dfe1ea 10%, #ebecf2 20%);
  background-size: 800px 100px;
  animation: GhostShimmer 1.2s forwards infinite linear;
}
@-moz-keyframes GhostShimmer {
  0% {
    background-position: -300px 0;
  }
  100% {
    background-position: 300px 0;
  }
}
@-webkit-keyframes GhostShimmer {
  0% {
    background-position: -300px 0;
  }
  100% {
    background-position: 300px 0;
  }
}
@-o-keyframes GhostShimmer {
  0% {
    background-position: -300px 0;
  }
  100% {
    background-position: 300px 0;
  }
}
@keyframes GhostShimmer {
  0% {
    background-position: -300px 0;
  }
  100% {
    background-position: 300px 0;
  }
}
