.Card {
  background-color: #fff;
  border-radius: 8px;
  padding: 20px;
}
.Card--shadow {
  box-shadow: 0 0 8px rgba(0,0,0,0.1);
}
.Card--animate {
  animation: fadeInUpAppear 0.24s cubic-bezier(0, 0, 0.22, 0.95) forwards;
}
@-moz-keyframes fadeInUpAppear {
  0% {
    opacity: 0;
    transform: translate(-50%, 3px);
  }
  100% {
    opacity: 1;
    transform: translate(-50%, 0);
  }
}
@-webkit-keyframes fadeInUpAppear {
  0% {
    opacity: 0;
    transform: translate(-50%, 3px);
  }
  100% {
    opacity: 1;
    transform: translate(-50%, 0);
  }
}
@-o-keyframes fadeInUpAppear {
  0% {
    opacity: 0;
    transform: translate(-50%, 3px);
  }
  100% {
    opacity: 1;
    transform: translate(-50%, 0);
  }
}
@keyframes fadeInUpAppear {
  0% {
    opacity: 0;
    transform: translate(-50%, 3px);
  }
  100% {
    opacity: 1;
    transform: translate(-50%, 0);
  }
}
