.FieldWrapper--RadioGroup {
  display: block;
}
.Field--RadioGroup .Field-el {
  display: block;
  padding: 0;
  border: none;
  cursor: pointer;
  line-height: initial;
}
.Field--RadioGroup .Radio-label,
.Field--RadioGroup .Radio-mark {
  display: inline-block;
  vertical-align: middle;
}
.Field--RadioGroup .Radio-mark {
  position: relative;
  width: 20px;
  height: 20px;
  border-radius: 50%;
  border: 1px solid #000;
  margin-right: 4px;
  text-align: center;
}
.Field--RadioGroup .Radio-mark::before {
  content: '';
  position: relative;
  display: inline-block;
  vertical-align: middle;
  width: 50%;
  height: 50%;
  border-radius: 50%;
  background: #00f;
  opacity: 0;
  transform: scale(0.5);
  transition: 0.1s cubic-bezier(0.08, 1.07, 1, 1);
}
.Field--RadioGroup input:checked + .Radio-mark::before {
  opacity: 1;
  transform: scale(1);
}
